/* global chrome */
import '../../assets/main.css'

import React, { Component } from 'react'
import { Settings } from 'react-feather'
import FlashMessage from 'react-flash-message'
import Clock from 'react-live-clock'

import Add from '../../assets/icons/add.png'
import AddTodosOverlay from '../../components/AddTodosOverlay'
import SearchBar from '../../components/SearchBar'
import SettingsTray from '../../components/SettingsTray'
import SlideShow from '../../components/SlideShow'

// const getWorkMode = localStorage.getItem('WorkMode')
export default class Main extends Component {
  constructor() {
    super()
    this.state = {
      todos: '',
      userName: chrome.storage.sync.get(['username'], (username) => {
        return username
      }),
      searchError: false,
      showSettings: false,
      showAddTodosOverlay: false,
      showAddBtn: true,
      finished: chrome.storage.sync.get(['Finished'], (finished) => {
        return finished
      }),
      workMode: chrome.storage.sync.get(['WorkMode'], (workMode) => {
        return workMode === null ? false : workMode
      })
    }
  }

  componentDidMount() {
    this.interval = setInterval(() => {
      this.checkTodos()
    }, 1)
  }

  componentWillUnmount() {
    clearInterval(this.interval)
  }

  // Search error handler for flash message
  searchErrorHandler = () => {
    this.setState({ searchError: true })
  }

  // Function that runs continuously to check if todos array exists in state
  checkTodos = () => {
    // const todosLocal = localStorage.getItem('Todos')
    // const todos = JSON.parse(todosLocal)
    chrome.storage.sync.get(['Todos'], (todos) => {
      const todosStore = JSON.stringify(todos)
      this.setState({ todos: todosStore })
      if (todos) {
        return this.setState({
          showAddTodosOverlay: false,
          showAddBtn: false
        })
      } else {
        return this.setState({
          showAddBtn: true
        })
      }
    })
  }

  mainWorkModeToggle = () => {
    this.setState({ workMode: !this.state.workMode })
  }

  // Overlay for adding todos toggle handler
  addTodosOverlayHandler = () => {
    this.setState((prevState) => ({
      showAddTodosOverlay: !prevState.showAddTodosOverlay
    }))
  }

  // Settings Tray Handler
  openSettingsHandler = () => {
    this.setState({ showSettings: true })
  }

  closeSettingsHandler = () => {
    this.setState({ showSettings: false })
  }

  // Function for marking complete todo
  markCompleted = (e) => {
    const { id } = e.target
    this.setState({
      todos: this.state.todos.map((todo) => {
        if (todo.id === id) {
          todo.completed = !todo.completed
        }
        return todo
      })
    })
    const { todos } = this.state
    // console.log(todos.length)

    // localStorage.setItem('Todos', todosStore)
    chrome.storage.sync.set({ Todos: todos })
    this.checkFinished()
  }

  // Function called when Marking complete todo item so done button can appear
  checkFinished = () => {
    const { todos } = this.state
    const filtered = todos.filter((todo) => todo.completed === false)
    if (filtered.length === 0) {
      // localStorage.setItem('Finished', true)
      chrome.storage.sync.set({ Finished: true })
      // const finished = localStorage.getItem('Finished')
      chrome.storage.sync.get(['Finished'], (finished) => {
        return this.setState({ finished })
      })
    } else {
      // localStorage.setItem('Finished', false)
      chrome.storage.sync.set({ Finished: false })
      // const finished = localStorage.getItem('Finished')
      chrome.storage.sync.get(['Finished'], (finished) => {
        return this.setState({ finished })
      })
    }
  }

  // Done Button function
  clearTodos = () => {
    // localStorage.removeItem('Todos')
    chrome.StorageArea.remove('Todos')
    // localStorage.setItem('Finished', false)
    chrome.storage.sync.set({ Finished: false })
    this.setState({ finished: false })
  }

  render() {
    const {
      showSettings,
      userName,
      showAddTodosOverlay,
      todos,
      showAddBtn,
      finished,
      searchError,
      workMode
    } = this.state
    return (
      <div className="w-100 vh-100 d-flex flex-column justify-content-center align-items-center animated fadeIn main">
        {/* Time */}
        <h1 className="time p-3">
          <Clock format="h:mma" interval={1000} />
        </h1>

        {/* Work mode message */}
        {workMode && (
          <h3 className="work-mode mt-10 bg-warning rounded p-3" style={{ opacity: '0.75' }}>
            You're in Work mode
          </h3>
        )}

        {/* add Todos Overlay */}
        {showAddTodosOverlay && (
          <AddTodosOverlay
            addTodosOverlayHandler={this.addTodosOverlayHandler}
            showAddTodosOverlay={showAddTodosOverlay}
            userName={userName}
          />
        )}

        {/* flash message */}
        {searchError ? (
          <FlashMessage duration={3000}>
            <small className="bg-white animated shake top-0 p-1 mb-3" style={{ color: 'red', opacity: '0.6' }}>
              Enter search question and click on the platform(s) you want to search on
            </small>
          </FlashMessage>
        ) : null}
        <SearchBar searchErrorHandler={this.searchErrorHandler} workMode={workMode} />
        <div className="mt-9" />

        {/* carousel */}
        {workMode ? null : <SlideShow />}

        {/* Todo card */}
        {todos && (
          <div className={workMode ? 'mt-4 border bg-white' : 'todos-card float-right mt-1 mr-2 bg-white'}>
            <div className="card-content shadow" style={{ width: '15rem' }}>
              <div className="card-body p-1">
                {finished ? (
                  <h6 className="card-title text-center text-uppercase">Good job {userName}</h6>
                ) : (
                  <h6 className="card-title text-center text-uppercase">{userName}'s Todos</h6>
                )}
              </div>
              <ul className="list-group">
                {todos.map((todo) => (
                  <li
                    key={todo.id}
                    className={
                      todo.completed
                        ? 'list-group-item p-3 pl-3 pr-3 line-through animated shake'
                        : 'list-group-item p-3 pl-3 pr-3'
                    }
                    style={{ fontSize: '0.85rem' }}
                  >
                    <input
                      type="checkbox"
                      onChange={this.markCompleted}
                      id={todo.id}
                      className="float-right"
                      style={{ cursor: 'pointer' }}
                      checked={todo.completed ? true : false}
                    />
                    {todo.title}
                  </li>
                ))}
                {finished && (
                  <button className="btn-success p-2 animated fadeIn border-0" onClick={this.clearTodos}>
                    Done
                  </button>
                )}
              </ul>
            </div>
          </div>
        )}

        {/* Add Todos Button */}
        {showAddBtn &&
          (workMode ? (
            <img
              className="mr-2 mt-2"
              title={`Add Todos ${userName}`}
              src={Add}
              alt="add"
              onClick={this.addTodosOverlayHandler}
              style={{ cursor: 'pointer' }}
            />
          ) : (
            <img
              className="add-btn mr-2 mt-2"
              title={`Add Todos ${userName}`}
              style={{ width: '40px', height: '40px' }}
              src={Add}
              alt="add"
              onClick={this.addTodosOverlayHandler}
            />
          ))}

        {/* settings */}
        <SettingsTray
          showSettings={showSettings}
          mainWorkModeToggle={this.mainWorkModeToggle}
          userName={userName}
          workMode={workMode}
          closeSettingsHandler={this.closeSettingsHandler}
        />
        <Settings
          onClick={this.openSettingsHandler}
          className="fixed-bottom ml-3 mb-3"
          size="30"
          style={{ cursor: 'pointer' }}
        />
      </div>
    )
  }
}
